<template>

    <fieldset id="authProxy" v-if="isSilent">
        <legend>()</legend>
        <div>
            T-DASH<br>
        </div>
        <div class="form-warn">
            <br>
            1. <br>
            2. XX_win<br>
            3. <br>
            4. 
        </div>
        <div class="form-area">
            <label for="authProxyAddress"></label><br>
            <input type="text" class="input-text" id="authProxyAddress" v-model="inputedAuthProxy.address"
                name="authProxyAddress" @input="updateOpt" placeholder="xxx.xxx.xxx.xxx"><br>
            <label for="authProxyPort"></label><br>
            <input type="text" class="input-text" id="authProxyPort" v-model="inputedAuthProxy.port"
                name="authProxyPort" @input="updateOpt" placeholder="8888"><br>
            <label for="authProxyAccount"></label><br>
            <input type="text" class="input-text" id="authProxyAccount" v-model="inputedAuthProxy.account"
                name="authProxyAccount" @input="updateOpt" placeholder="your_account"><br>
            <label for="authProxyPassword"></label><br>
            <input type="text" class="input-text" id="authProxyPassword" v-model="inputedAuthProxy.password"
                name="authProxyPassword" @input="updateOpt" placeholder="your_password"><br>
        </div>
    </fieldset>
</template>

<script>
import { onMounted, reactive, watch } from 'vue';
import makeOpt from '@/js/makeOpt.js'
import cacheUtils from '@/js/cacheUtils';

export default {
    name: 'AuthProxyForm',
    props: {
        isSilent: { type: Boolean, default: true }
    },
    setup(props, { emit }) {
        // ###############
        //  #
        // ###############
        const inputedAuthProxy = reactive({
            address: '',
            port: '',
            account: '',
            password: ''
        })

        // #############
        //  #
        // #############
        const updateOpt = async () => {
            // 
            const createdOpt = await makeOpt.createProxyOpt(
                "auth", inputedAuthProxy, props.isSilent
            )

            // cache
            await cacheUtils.saveData("authProxyAddress", inputedAuthProxy.address)
            await cacheUtils.saveData("authProxyPort", inputedAuthProxy.port)
            await cacheUtils.saveData("authProxyAccount", inputedAuthProxy.account)
            await cacheUtils.saveData("authProxyPassword", inputedAuthProxy.password)

            // &
            emit("update-opt", "authProxy", createdOpt)
        }

        // ############
        // props #
        // * propscomputedwatch
        // ############
        // 
        watch(
            () => props.isSilent,
            async () => {
                await updateOpt()
            }
        )

        // ###############
        //  #
        // ###############
        onMounted(async () => {
            // cache
            inputedAuthProxy.address = await cacheUtils.getData("authProxyAddress")
            inputedAuthProxy.port = await cacheUtils.getData("authProxyPort")
            inputedAuthProxy.account = await cacheUtils.getData("authProxyAccount")
            inputedAuthProxy.password = await cacheUtils.getData("authProxyPassword")

            await updateOpt()
        })

        return {
            // 
            inputedAuthProxy,
            // 
            updateOpt,
        }
    }
}
</script>
