<template>
    <!--  -->
    <fieldset id="silent">
        <legend></legend>
        <div>
            <br>
            <br>
            
        </div>
        <div class="form-area">
            <input type="radio" class="input-radio" id="notSilentRadio" v-model="silentFormData" name="silentRadio"
                :value="false" @change="updateOpt">
            <label for="notSilentRadio">: </label><br>
            <input type="radio" class="input-radio" id="silentRadio" v-model="silentFormData" name="silentRadio"
                :value="true" @change="updateOpt" checked>
            <label for="silentRadio">: ()</label><br>
        </div>
    </fieldset>
</template>

<script>
import { onMounted, ref } from 'vue';

import cacheUtils from '@/js/cacheUtils';

export default {
    name: 'SilentForm',
    setup(_, { emit }) {
        // ###############
        //  #
        // ###############
        const silentFormData = ref(true)

        // #############
        //  #
        // #############
        const updateOpt = async () => {
            // 
            let createdOpt = ""
            if (silentFormData.value) {
                createdOpt += "--silent"
            }

            // cache
            await cacheUtils.saveData("silent", silentFormData.value)

            // &
            emit("update-opt", "silent", createdOpt)
        }

        // ###############
        //  #
        // ###############
        onMounted(async () => {
            // cache
            silentFormData.value = await cacheUtils.getData("silent", true)

            await updateOpt()
        })

        return {
            // 
            silentFormData,
            // 
            updateOpt,
        }
    }
}
</script>
