<template>
    <!--  -->
    <fieldset id="testrunName">
        <legend></legend>
        <div>
            <br>
            sync<br>
            show_testrun<br>
            
        </div>
        <div class="form-area">
            <v-btn class="add-form click-cursor" @click="addForm"></v-btn>
            <!--  -->
            <div v-for="(form, index) in inputedTestrunNames" :key="index" class="name-form">
                <label for="notSilentRadio">{{ index + 1 }}. : </label>
                <input type="text" class="input-text-row" v-model="form.name" @input="updateOpt"
                    placeholder="">
                <!--  -->
                <v-btn class="delete-form click-cursor" @click="removeForm(index)"></v-btn>
            </div>
        </div>
    </fieldset>
</template>

<script>
import { computed, onMounted, reactive } from 'vue';

import cacheUtils from '@/js/cacheUtils';

export default {
    name: 'TestrunNameForm',
    setup(_, { emit }) {
        // ###############
        //  #
        // ###############
        const inputedTestrunNames = reactive([{ name: '' }]);

        // ###################################
        // QT #
        // ###################################
        const testrunNameList = computed(() => {
            const retList = []
            for (let i = 0; i < inputedTestrunNames.length; i++) {
                if (inputedTestrunNames[i].name) {
                    retList.push(inputedTestrunNames[i].name)
                }
            }
            return retList
        })

        // ########################
        //  #
        // ########################
        // 
        const addForm = () => {
            inputedTestrunNames.push({ name: '' })
        };

        // 
        const removeForm = (index) => {
            if (inputedTestrunNames.length === 1) {
                return;
            }
            inputedTestrunNames.splice(index, 1)
            updateOpt()
        };


        // #############
        //  #
        // #############
        const updateOpt = async () => {
            // 
            let createdOpt = ""

            // 
            for (let i = 0; i < inputedTestrunNames.length; i++) {
                const inputedTestrunName = inputedTestrunNames[i]
                if (inputedTestrunName.name === "") {
                    continue
                }
                if (createdOpt) {
                    createdOpt += "/"
                }
                createdOpt += inputedTestrunName.name
            }

            // cache
            await cacheUtils.saveData("testrunNames", inputedTestrunNames)

            // &
            emit("update-opt", "testrunName", createdOpt)
            emit("update-qt-testrun", testrunNameList.value)
        }

        // ###############
        //  #
        // ###############
        onMounted(async () => {
            // cache 
            const cacheTestrunNames = await cacheUtils.getData("testrunNames", [{ name: '' }])
            // reactive
            Object.assign(inputedTestrunNames, cacheTestrunNames);

            await updateOpt()
        })

        return {
            // 
            inputedTestrunNames,
            // 
            updateOpt,
            // 
            addForm,
            removeForm,

        }
    }
}
</script>

<style scoped>
.add-form {
    background-color: rgb(0, 156, 225);
    color: white;
    padding: 6px;
    margin-bottom: 8px;
    border: unset;
}

.add-form:hover {
    background-color: rgb(2, 135, 192);
}

.delete-form {
    background-color: #ff4900;
    color: white;
    padding: 3px 6px;
    margin-left: 8px;
    border: unset;
    height: max-content !important;
    bottom: 2px;
}

.delete-form:hover {
    background-color: #ba3501;
}

.name-form {
    margin-bottom: 8px;
}

.name-form:last-child {
    margin-bottom: unset;
}

.input-text-row {
    width: 70%;
}
</style>