<template>
    <!--  -->
    <fieldset id="testProxy" v-if="isSilent">
        <legend>()</legend>
        <div>
            
        </div>
        <div class="form-warn">
            <br>
            1. <br>
            2. <br>
            3. <br>
            4. 
            <a href="https://service.valtes.co.jp/t-dash/function/tutorial/testscenarios_test-cases_vol_007"
                target="_blank">
                
            </a>
        </div>
        <div class="form-area">
            <label for="testProxyAddress"></label><br>
            <input type="text" class="input-text" id="testProxyAddress" v-model="inputedTestProxy.address"
                name="testProxyAddress" @input="updateOpt" placeholder="xxx.xxx.xxx.xxx"><br>
            <label for="testProxyPort"></label><br>
            <input type="text" class="input-text" id="testProxyPort" v-model="inputedTestProxy.port"
                name="testProxyPort" @input="updateOpt" placeholder="8888"><br>
            <label for="testProxyAccount">URL</label><br>
            <input type="text" class="input-text" id="testProxyAccount" v-model="inputedTestProxy.url"
                name="testProxyAccount" @input="updateOpt" placeholder="https://xxxx.xxx"><br>
        </div>
    </fieldset>
</template>

<script>
import { onMounted, reactive, watch } from 'vue';

import makeOpt from '@/js/makeOpt.js'
import cacheUtils from '@/js/cacheUtils';

export default {
    name: 'TestrunProxy',
    props: {
        isSilent: { type: Boolean, default: true }
    },
    setup(props, { emit }) {
        // ###############
        //  #
        // ###############
        // 
        const inputedTestProxy = reactive({
            address: '',
            port: '',
            account: '',
            password: '',
            url: '',
        })

        // #############
        //  #
        // #############
        const updateOpt = async () => {
            // 
            let createdOpt = ""

            // 
            createdOpt += await makeOpt.createProxyOpt("test", inputedTestProxy, props.isSilent)

            // cache
            await cacheUtils.saveData("testProxyAddress", inputedTestProxy.address)
            await cacheUtils.saveData("testProxyPort", inputedTestProxy.port)
            await cacheUtils.saveData("testProxyAccount", inputedTestProxy.account)
            await cacheUtils.saveData("testProxyPassword", inputedTestProxy.password)
            await cacheUtils.saveData("testProxyUrl", inputedTestProxy.url)

            // &
            emit("update-opt", "testProxy", createdOpt)
        }

        // ############
        // props #
        // * propscomputedwatch
        // ############
        // 
        watch(
            () => props.isSilent,
            async () => {
                await updateOpt()
            }
        )

        // ###############
        //  #
        // ###############
        onMounted(async () => {
            // cache
            inputedTestProxy.address = await cacheUtils.getData("testProxyAddress")
            inputedTestProxy.port = await cacheUtils.getData("testProxyPort")
            inputedTestProxy.account = await cacheUtils.getData("testProxyAccount")
            inputedTestProxy.password = await cacheUtils.getData("testProxyPassword")
            inputedTestProxy.url = await cacheUtils.getData("testProxyUrl")

            await updateOpt()
        })

        return {
            // 
            inputedTestProxy,
            // 
            updateOpt,

        }
    }
}
</script>
