<template>
    <!--  -->
    <fieldset id="reportDir">
        <legend>()</legend>
        <div>
            <br>
            
        </div>
        <div class="form-warn">
            <br>
            1. reports<br>
            <span style="margin-left: 18px;"></span><br>
            <span style="margin-left: 18px;"></span>Web :commandlinetestrun<br>
            <span style="margin-left: 18px;"></span>Windows :commandlinetestpjwin
        </div>
        <div class="form-area">
            <label for="reportDir"></label>
            <PathHelp /><br>
            <input id="reportDir" type=" text" class="input-text" v-model="inputedReportDir" name="reportDir"
                @input="updateOpt" placeholder="/Path/to/your_reports">
        </div>
    </fieldset>
</template>

<script>
import { onMounted, ref } from 'vue';

import strUtils from '@/js/strUtils';
import cacheUtils from '@/js/cacheUtils';


export default {
    name: 'ReportDirForm',
    setup(_, { emit }) {
        // ###############
        //  #
        // ###############
        const inputedReportDir = ref("")

        // #############
        //  #
        // #############
        const updateOpt = async () => {
            let createdOpt = ""

            // 
            if (inputedReportDir.value) {
                inputedReportDir.value = await strUtils.replaceSlush(inputedReportDir.value)
                createdOpt += ` --output_report_dir_path ${inputedReportDir.value}`
            }

            // cache
            await cacheUtils.saveData("reportsDir", inputedReportDir.value)

            // &
            emit("update-opt", "reportsDir", createdOpt)
        }

        // ###############
        //  #
        // ###############
        onMounted(async () => {
            // cache
            inputedReportDir.value = await cacheUtils.getData("reportsDir")

            await updateOpt()
        })

        return {
            // 
            inputedReportDir,
            // 
            updateOpt,
        }
    }
}
</script>
